th {
  text-align: center;
}
td {
  padding: 5px 10px;
  cursor: default;
  user-select: none;
  min-width: 40px;
  &.isset {
    text-align: center;
    background-color: #060;
    color: white;
    cursor: pointer;
    &:hover {
      background-color: #090;
    }
  }
  &.empty {
    text-align: center;
    background-color: #A00;
    color: white;
    cursor: pointer;
    &:hover {
      background-color: #F00;
    }
  }
}
.button {
  display: inline-flex;
  height: 34px;
  justify-content: center;
  align-items: center;
}