th {
  text-align: center;
}
td {
  padding: 5px 10px;
  cursor: default;
  user-select: none;
  min-width: 40px;
}
td.isset {
  text-align: center;
  background-color: #060;
  color: white;
  cursor: pointer;
}
td.isset:hover {
  background-color: #090;
}
td.empty {
  text-align: center;
  background-color: #A00;
  color: white;
  cursor: pointer;
}
td.empty:hover {
  background-color: #F00;
}
.button {
  display: inline-flex;
  height: 34px;
  justify-content: center;
  align-items: center;
}
